import {createSlice, PayloadAction} from "@reduxjs/toolkit";

interface privateMessageState {
    allDialogsUsers: [{
        id: number,
        userName: string,
        hasNewMessages: boolean,
        lastDialogActivityDate: string,
        lastUserActivityDate: string,
        newMessagesCount: number,

        photos: {
            small: string,
            large: string
        },
        lastMessage: string
    }],
    fullDialogWithId: [
        {
            id: number,
            body: string,
            translatedBody: null,
            addedAt: string,
            senderId: number,
            senderName: string,
            recipientId: number,
            viewed: false
        }
    ],

    rerender: number,
    newMessages: number,
}

const initialState: any = {
    allDialogsUsers: [{
        id: 0,
        userName: '',
        hasNewMessages: false,
        lastDialogActivityDate: '',
        lastUserActivityDate: '',
        newMessagesCount: 0,

        photos: {
            small: '',
            large: ''
        },
        lastMessage: ''
    }],
    fullDialogWithId: [
        {
            id: 0,
            body:'',
            translatedBody: null,
            addedAt: '',
            senderId: 0,
            senderName: '',
            recipientId: 0,
            viewed: false
        }
    ],

    rerender: 0,
    newMessages: 0,
}

export const privateMessageSlice = createSlice({
    name: 'privateMessage', initialState, reducers: {
        getAllDialogsAction(state, action: PayloadAction<any>) {
            state.allDialogsUsers = action.payload
        },
        setFullDialogsWithIdAction(state, action: PayloadAction<any>) {
            state.fullDialogWithId = action.payload
        },
    }
})


export default privateMessageSlice.reducer