import {NavLink} from "react-router-dom";
import {
    Box,
    Stack,
    Table,
    TableContainer,
    Typography,
    Avatar,
    Paper
} from "@mui/material";
import React, {useEffect, useState} from "react";
import {checkPathname} from "../../utils/checkPathname";


const MessageOther = (props: any) => {
    return (
        <Stack direction={'row'} justifyContent={'left'} spacing={2} sx={{width: '100%', backgroundColor: (props.index === 0) ? 'rgba(16,230,27,0.1)' : 'none'}}
               marginBottom={3}>
            <Stack direction={'column'} justifyContent={'center'}>
                <Avatar alt="Remy Sharp" src={props.photo}/>
                <Typography>{props.name}</Typography>
            </Stack>

            <Stack direction={'column'} justifyContent={'center'} sx={{width: '100%'}}>
                <Paper elevation={3}>
                    <Typography sx={{wordBreak: 'break-all', backgroundColor: (props.index === 0) ? 'rgba(16,230,27,0.1)' : 'none'}} padding={2}>{props.message}</Typography>
                </Paper>
            </Stack>
        </Stack>
    )
}


export const PrivateMessages = (props: any) => {
    console.log('props')
    console.log(props)

    /** PrivateMessagePage **/
    //load all users
    // useEffect(() => {
    //     console.log('getDialogs')
    //     if (props.allDialogsUsers.length < 2)
    // }, [])

    useEffect(() => {
        props.getAllDialogsThunk()
        // sideEffect_setScrollDown('messageArea')

        if (checkPathname() === '/PrivateMessage'){
            const timer = setInterval(() => {
                props.getAllDialogsThunk()
            }, 10000);
            return () => clearInterval(timer);
        }

    }, []);

    if (props.allDialogsUsers.length < 2) return<Box flex={5} p={2}>
        <Stack direction={'row'} justifyContent={'center'}>
            <div>Загрузка сообщений...</div>
        </Stack>
    </Box>

    return <Box flex={5} p={2}>
        <Stack direction={'row'} justifyContent={'space-between'}>
            {/*message place*/}
            <Box flex={10}>
                <Stack height={'100%'} direction={'column'} justifyContent={'space-between'}>
                    <Box flex={10} marginRight={4}>
                        <Paper sx={{height: '100%', width: '100%'}}>
                            <Stack direction={'row'} justifyContent={'space-between'} padding={3}>
                                <Stack direction={'column'} justifyContent={'space-between'} sx={{width: '100%'}}>
                                    {/*view all messages*/}
                                    <TableContainer
                                        id={'messageArea'}
                                        sx={{
                                            height: 'calc(75vh)',
                                            'overflow-y': 'scroll',
                                            'scrollbar-color': '#1976d2 white',
                                            'scrollbar-width': 'thin'
                                        }}
                                    >
                                        <Box sx={{height: "max-content"}}>
                                            {
                                                props.allDialogsUsers.map((u: any, index: any) => {

                                                    return <NavLink key={index} to={{pathname: '/PrivateMessage/PrivText/' + u.id}}
                                                                    state={{senderPhotoById: u.photos.small ? u.photos.small : ''}}><MessageOther
                                                        key={index} id={u.id} name={u.userName} photo={u.photos.small ? u.photos.small : ''}
                                                        message={u.lastMessage}
                                                        index={index}
                                                        color={(props.newMessageStatus !== 0 && index === 0) ? 'rgba(16,230,27,0.1)' : 'none'}/></NavLink>
                                                })
                                            }
                                        </Box>
                                    </TableContainer>
                                </Stack>
                            </Stack>
                        </Paper>
                    </Box>
                    <Box color={'white'}>fix here bug</Box>
                </Stack>
            </Box>
        </Stack>


    </Box>
}