import {NavLink} from "react-router-dom";
import {
    Box,
    Stack,
    Table,
    TableContainer,
    TextField,
    Typography,
    Avatar,
    Paper
} from "@mui/material";
import SendIcon from '@mui/icons-material/Send';
import React, {useEffect, useRef} from "react";
import style from './Chat.module.scss'
import {MessageWebSocket} from "../../App";
import {checkPathname} from "../../utils/checkPathname";

const MessageOther = (props: any) => {
    return (
        <Stack className={style.fullWidth} direction={'row'} justifyContent={'left'} spacing={2} marginBottom={3}>
            <Stack direction={'column'} justifyContent={'center'}>
                <Avatar alt="Remy Sharp" src={props.photo}/>
                <Typography>{props.name}</Typography>
            </Stack>

            <Stack className={style.fullWidth} direction={'column'} justifyContent={'center'}>
                <Paper elevation={3}>
                    <Typography className={style.wordBreak} padding={2}>{props.message}</Typography>
                </Paper>
            </Stack>
        </Stack>
    )
}

const MessageOwner = (props: any) => {
    return <Stack className={style.fullWidth} direction={'row'} justifyContent={'left'} spacing={2}
                  marginBottom={3}>
        <Stack className={style.fullWidth} direction={'column'} justifyContent={'center'}>
            <Paper elevation={3}>
                <Typography className={style.wordBreak} padding={2}>{props.message}</Typography>
            </Paper>
        </Stack>

        <Stack direction={'column'} justifyContent={'center'}>
            <Avatar alt="Remy Sharp" src={props.photo}/>
            <Typography>{props.name}</Typography>
        </Stack>
    </Stack>
}


export const Chat = (props: any) => {
    useEffect(()=>{
        if (checkPathname() === '/Chat') {
            props.setShowSocketCat(false)
        }
    },[])

    const newMessage = useRef('')

    return <Box flex={5} p={2}>
        <Stack direction={'row'} justifyContent={'space-between'}>
            {/*message place*/}
            <Box flex={10}>
                <Stack height={'100%'} direction={'column'} justifyContent={'space-between'}>
                    <Box flex={10} marginRight={4}>
                        <Paper className={style.paper}>
                            <Stack direction={'row'} justifyContent={'space-between'} padding={3}>
                                <Stack className={style.fullWidth} direction={'column'} justifyContent={'space-between'}>

                                    <TableContainer className={style.tableContainer}
                                        id={'messageArea'}>
                                       <Box className={style.maxContent}>
                                            {
                                                props.messages.map((u: { userId: number, userName: string, photo: string, message: string }, index: number) => {
                                                    if (u.userId === props.authId) {
                                                        return <NavLink key={index} to={'/Profile/' + u.userId}>
                                                                    <MessageOwner key={index} id={u.userId} name={u.userName} photo={u.photo} message={u.message}/></NavLink>
                                                    } else {
                                                        return <NavLink key={index} to={'/Profile/' + u.userId}>
                                                                    <MessageOther key={index} id={u.userId} name={u.userName} photo={u.photo} message={u.message}/></NavLink>
                                                    }
                                                })
                                            }
                                        </Box>
                                    </TableContainer>
                                </Stack>
                            </Stack>
                        </Paper>
                    </Box>
                    <Box color={'white'}>fix here bug</Box>
                    {/*send message place*/}
                    <Box>
                        <Stack direction={'row'} justifyContent={'space-between'}>
                            <Box flex={32}><TextField onBlur={e => newMessage.current = e.target.value} label="You message" color="secondary" focused fullWidth/></Box>
                            <Box flex={1} marginLeft={1}>
                                <Stack className={style.fullHeight} direction={'column'} justifyContent={'center'}>
                                    <SendIcon onClick={e => {MessageWebSocket?.send(newMessage.current);newMessage.current = ''}}/>
                                </Stack>
                            </Box>
                        </Stack>
                    </Box>

                </Stack>

            </Box>
        </Stack>


    </Box>
}