import {connect} from "react-redux";
import React, {useEffect} from "react";
import {getAuthInformation, doLogout} from "../../redux/action-creators/authAction";
import {Navbar} from "./Navbar";

const NavbarContainer = (props: any) => {
    useEffect(()=>{
        props.getAuthInformation()

    },[])

    return <Navbar {...props}/>
}

const mapStateToProps = (state: any) => {
    return {
        id: state.authSlice.id,
        email: state.authSlice.email,
        login: state.authSlice.login,
        isAuth: state.authSlice.isAuth,
        avatarPhoto: {...{...state.profile}.photos}.small,
        authId: state.authSlice.id,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        getAuthInformation,
        doLogout

    }
}

const HeaderContainerr = connect(mapStateToProps, mapDispatchToProps)(NavbarContainer)

export default HeaderContainerr
