import React, {useEffect} from "react";
import {connect} from "react-redux";
import Profile from "./Profile";
import {compose} from "redux";
import {useParams} from "react-router-dom";
import {getProfileInformation} from "../../redux/action-creators/profileAction";
import {RootState} from "../../redux/store";


export type profilePropsType = ReturnType<typeof mapStateToProps> & typeof mapDispatchToPropsLite


const ProfileContainer = (props: profilePropsType) => {
    const params = useParams()

    useEffect(() => {
        props.getProfileInformation(Number(params.userId))
    }, [params.userId])

    return <Profile {...props}/>
}

const mapStateToProps = (state: RootState) => {
    return {
        ...state.profileSlice,
        authId: state.authSlice.id,
    }
}

const mapDispatchToPropsLite = {
    getProfileInformation,
}

export default compose(
    connect<any, any, any, any>(mapStateToProps, mapDispatchToPropsLite),
)(ProfileContainer)