import {Box, Paper, Stack, Typography} from "@mui/material";
import NoPhoto from "../../../assets/img/user.png";
import style from './ProfileTablet.module.scss'
import {profilePropsType} from "../ProfileContainer";
import {langTranslator} from "../../../utils/langTranslator";


export const ProfileTablet = (props: profilePropsType) => {
    return <Box className={style.genBox} flex={5} p={2}>
        <Stack direction={'row'} justifyContent={'space-between'}>

            <Box flex={10} >

                <Stack direction={'column'} justifyContent={'space-between'} spacing={8}>

                    <Box className={style.leftPanel} flex={1} >
                        {/*Good size 300x450*/}
                        <img className={style.photo} src={{...props.photos}.large ? {...props.photos}.large : NoPhoto}/>
                        <Typography className={style.aboutMe} variant={'h5'}> {props.fullName}</Typography>
                        <Typography className={style.aboutMe}>Обо мне: {props.aboutMe ? props.aboutMe : <span className={style.spanInfo}>не указано</span>}</Typography>

                    </Box>

                    <Box className={style.rightPanel} flex={3}>
                        <Paper className={style.paper}>
                            <Typography textAlign={'center'} variant={'h6'}>{props.fullName} <span className={style.spanSay}>говорит</span>: {props.status ? props.status : <span className={style.spanInfo}>не указано</span>}</Typography>

                            <br/>
                            <Typography>Про работу:</Typography>
                            <Typography className={style.aboutWork}>Ищу работу: {props.lookingForAJob ? "да" : 'нет'}</Typography>
                            <Typography className={style.aboutWork}>Навыки: {props.lookingForAJobDescription ? props.lookingForAJobDescription : <span className={style.spanInfo}>не указано</span>}</Typography>

                            <br/>
                            <Typography>Социальные сети:</Typography>
                            {Object.keys({...props.contacts}).map(key => {
                                const rusWord = langTranslator(key)
                                return <Typography className={style.socialNetwork} key={key}>{rusWord}: <span className={style.spanInfo}>{{...props.contacts}[key] ? {...props.contacts}[key] : 'не указано'}</span></Typography>
                            })}
                        </Paper>
                    </Box>

                </Stack>

            </Box>

            
        </Stack>

    </Box>
}