import React from "react";
import {useMediaQuery} from "react-responsive";
import {NavbarDesktop} from "./desktop/NavbarDesktop";
import {NavbarMobile} from "./mobile/NavbarMobile";
import {NavbarTablet} from "./tablet/NavbarTablet";
import {
    useIsDesktop,
    useIsMobile,
    useIsPortrait,
    useIsRetina,
    useIsTabletMax,
    useIsTabletMin
} from "../../hooks/responsive";
import {FailLoading} from "../common/FailLoading/FailLoading";

export const Navbar = (props: any) => {
    const isDesktop = useIsDesktop()

    const isTabletMin = useIsTabletMin()
    const isTabletMax = useIsTabletMax()
    const isTablet = isTabletMin && isTabletMax

    const isMobile = useIsMobile()

    const isPortrait = useIsPortrait()
    const isRetina = useIsRetina()

    if (isDesktop) console.log('isDesktop')
    if (isTablet) console.log('isTablet')
    if (isMobile) console.log('isMobile')


    if(isDesktop) return <NavbarDesktop {...props}/>
    if(isTablet) return <NavbarTablet {...props}/>
    if(isMobile) return <NavbarMobile {...props}/>
    return <FailLoading/>
}