import {ProfileDesktop} from "./desktop/ProfileDesktop";
import {ProfileTablet} from "./tablet/ProfileTablet";
import {ProfileMobile} from "./mobile/ProfileMobile";
import {
    useIsDesktop,
    useIsMobile,
    useIsPortrait,
    useIsRetina,
    useIsTabletMax,
    useIsTabletMin
} from "../../hooks/responsive";
import {profilePropsType} from "./ProfileContainer";
import {FailLoading} from "../common/FailLoading/FailLoading";

const Profile = (props: profilePropsType) => {
    const isDesktop = useIsDesktop()

    const isTabletMin = useIsTabletMin()
    const isTabletMax = useIsTabletMax()
    const isTablet = isTabletMin && isTabletMax

    const isMobile = useIsMobile()

    const isPortrait = useIsPortrait()
    const isRetina = useIsRetina()

    if (isDesktop) console.log('isDesktop')
    if (isTablet) console.log('isTablet')
    if (isMobile) console.log('isMobile')

    if (isDesktop) return <ProfileDesktop {...props}/>
    if (isTablet) return <ProfileTablet {...props}/>
    if (isMobile) return <ProfileMobile {...props}/>

    return <FailLoading/>


}
export default Profile

