import {
    AppBar,
    Box,
    Stack,
    Toolbar,
    Typography
} from "@mui/material";
import React from "react";
import {NavLink} from "react-router-dom";
import devIcon from '../../../assets/img/devIcon.png'
import style from './NavbarDesktop.module.scss'

export const NavbarDesktop = (props: any) => {
    return <AppBar position={"sticky"}>
        <Toolbar className={style.toolbar} >
            <Stack direction={'row'} spacing={1}>
                <img className={style.image} src={devIcon}/>
                <Typography component={'p'}>SamuraiJS</Typography>
            </Stack>
            {
                props.isAuth
                    ? <Box><Typography className={style.cursor} onClick={props.doLogout()}>Выйти</Typography></Box>
                    : <NavLink className={style.linkDecorate} to={'/login'}>Войти</NavLink>
            }
        </Toolbar>
    </AppBar>
}