import {chatAPI} from "../../api/axiosAPI";
import {AppDispatch} from "../store";
import {appSlice} from "../reducers/appReducer";
import {privateMessageSlice} from "../reducers/privateMessageReducer";

export const getLastMessageByIdThunk = (id: any) => async (dispatch: AppDispatch) => {
    try {
        console.log('inside getLastMessageByIdThunk')
        const response = await chatAPI.getDialogsWithID(id)

        if (response) return {...response}.items[{...response}.items.length-1]
    } catch (e: any) {
        console.log('getLastMessageByIdThunk error => ' + e.message)
    }
}

export const getAllDialogsThunk = () => async (dispatch: AppDispatch) => {
    try {

        const allDialogs = await chatAPI.getAllDialogs()
        if(allDialogs){
            async function main(){
                const sleep = (m: any) => new Promise(r => setTimeout(r, m));

                let newArray = []; //to simulate the behavior of Array.map();
                let i = 0
                for(const u of allDialogs){


                    if(i < 6){

                        await sleep(200);
                        const lastMessage: any = await dispatch(getLastMessageByIdThunk(u.id))
                        if(lastMessage) {
                            console.log('lastMessage')
                            console.log(lastMessage)
                            newArray.push(({...u, lastMessage: lastMessage.body}))
                        }
                    } else {
                        newArray.push(({...u, lastMessage: '[...]', photos: {small:'',large:''}}))
                    }
                    i = i +1;

                }
                return newArray
            }

            const resp = await main()
            if(resp) dispatch(privateMessageSlice.actions.getAllDialogsAction(resp))

        }
    } catch (e: any) {
        console.log('getAllDialogsThunk error => ' + e.message)
    }
}

export const sendMessageById = (userId: any, message: any) => async (dispatch: AppDispatch) => {
    try {
       await chatAPI.postSendMessageToID(userId, message)
    } catch (e: any) {
        console.log('sendMessageById error => ' + e.message)
    }


}

export const setFullDialogsWithIdThunk = (id: any) => async (dispatch: AppDispatch) => {
    try {
        const response = await chatAPI.getDialogsWithID(id)

        if (response) dispatch(privateMessageSlice.actions.setFullDialogsWithIdAction(response.items))
    } catch (e: any) {
        console.log('setFullDialogsWithIdThunk error => ' + e.message)
    }
}