import React from "react";
import {PrivateMessages} from "./PrivateMessages";
import {compose} from "redux";
import {connect} from "react-redux";
import {useParams} from "react-router-dom";
import {PrivText} from "./PrivText/PrivText";
import {
    getAllDialogsThunk,
    sendMessageById,
    setFullDialogsWithIdThunk
} from "../../redux/action-creators/privateMessageAction";

const PrivateMessagesContainer = (props: any) => {
    const params = useParams()

    if(!params.recipientId) return <PrivateMessages {...props}/>
    if(params.recipientId) return <PrivText {...props}/>
    return <div/>

}

const mapStateToProps = (state: any): any => {
    return {
        ...state.privateMessageSlice,
        authId: state.authSlice.id,
        newMessageStatus: state.appSlice.newMessageStatus,
        smallPhotoOwner: state.profileSlice.photos.small,
    }
}

const mapDispatchToPropsLite: any = {
    //PrivMessagesThunks
    getAllDialogsThunk,
    // setAllLastMessageThunk,

    //PrivTextThunk
    setFullDialogsWithIdThunk,
    sendMessageById,

    //appThunk
    // getNewMessageCount,
    // getMessagesById


}


export default compose(
    connect(mapStateToProps, mapDispatchToPropsLite),
)(PrivateMessagesContainer)