import {createSlice, PayloadAction} from "@reduxjs/toolkit";

interface usersStateType {
    users: [{
        name: string,
        id: number,
        uniqueUrlName: null,
        photos: {
            small: string,
            large: string
        },
        status: string,
        followed: boolean,
    }],
    currentPage: number,
    pageSize: number,
    totalUserCount: number,
}

const initialState: usersStateType = {
    users: [{
        name: "maksuchiha",
        id: 27367,
        uniqueUrlName: null,
        photos: {
            small: '',
            large: ''
        },
        status: '',
        followed: false,
    }],
    currentPage: 1,
    pageSize: 100,
    totalUserCount: 20,
}

export const usersSlice = createSlice({
    name: 'users', initialState, reducers: {
        setFollow(state, action: PayloadAction<number>) {
            state.users.map((u: any, index: number) => {
                if (u.id === action.payload) state.users[index].followed = true
            })
        },

        setUnfollow(state, action: PayloadAction<number>) {
            state.users.map((u: any, index: number) => {
                if (u.id === action.payload) state.users[index].followed = false
            })
        },

        setArrayUsers(state, action: PayloadAction<typeof state.users>) {
            state.users = action.payload

        },

        setTotalUsersCount(state, action: PayloadAction<number>) {
            state.totalUserCount = action.payload
        },

        setCurrentPageNumber(state, action: PayloadAction<number>) {
            state.currentPage = action.payload
        },
    }
})


export default usersSlice.reducer