import {connect} from "react-redux";
import {Login} from "./Login";
import {doLogin} from "../../redux/action-creators/authAction";

const LoginContainer = (props: any) => {
    return <Login {...props}/>
}
const mapStateToProps = (state: any) => {
    return{
        isAuth: state.authSlice.isAuth,
        id: state.authSlice.id

    }

}
export default connect(mapStateToProps,{doLogin})(LoginContainer)