import {chatAPI} from "../../api/axiosAPI";
import {AppDispatch} from "../store";
import {appSlice} from "../reducers/appReducer";

export const setAppIsOnline = (statusApp: boolean) => async (dispatch: AppDispatch) => {
    try {
        console.log('app offline')
        dispatch(appSlice.actions.setAppIsOnline(statusApp))
    } catch (e: any) {
        console.log('getNewMessageCount error => ' + e.message)
    }
}


export const getNewMessageCount = () => async (dispatch: AppDispatch) => {
    try {
        const response = await chatAPI.getNewMessageCount()

        if (response) dispatch(appSlice.actions.setNewMessageCount(response))
    } catch (e: any) {
        console.log('getNewMessageCount error => ' + e.message)
    }
}

