import {combineReducers, configureStore} from "@reduxjs/toolkit";
import profileSlice from "./reducers/profileReducer";
import usersSlice from "./reducers/usersReducer";
import authSlice from "./reducers/authReducer";
import privateMessageSlice from "./reducers/privateMessageReducer";
import appSlice from "./reducers/appReducer";
import {reducer as formReducer} from 'redux-form'
import chatSlice from "./reducers/chatReducer";

const rootReducer = combineReducers({
    profileSlice,
    usersSlice,
    authSlice,
    appSlice,
    chatSlice,

    // privateMessageReducer,
    privateMessageSlice,
    form: formReducer,
})

export const setupStore = () => {
    return configureStore({
        reducer: rootReducer
    })
}

export type RootState = ReturnType<typeof rootReducer>
export type AppStore = ReturnType<typeof setupStore>
export type AppDispatch = AppStore['dispatch']