import React, {useEffect, useRef, useState} from "react";
import {Chat} from "./Chat";
import {compose} from "redux";
import {connect} from "react-redux";
import {
    setMessages,
    setShowSocketCat
} from "../../redux/action-creators/chatAction";

const ChatContainer = (props: any) => {
    return <Chat messages={props.messages} MessageWebSocket={props.MessageWebSocket} authId={props.authId} {...props}/>
}



const mapStateToProps = (state: any): any => {
    return {
        authId: state.authSlice.id,
        messages: state.chatSlice.messages,
        appIsOnline: state.appSlice.appIsOnline
    }
}

const mapDispatchToPropsLite: any = {
    setMessages,
    setShowSocketCat
}


export default compose(
    connect(mapStateToProps, mapDispatchToPropsLite)
)(ChatContainer)