import React, {useEffect} from "react";
import {connect} from "react-redux";
import {
    getProfileInformation,
    putProfilePhoto,
    putProfileStatus, putProfileInformation
} from "../../redux/action-creators/profileAction";
import {compose} from "redux";
import Settings from "./Settings";
import {useParams} from "react-router-dom";
import {RootState} from "../../redux/store";

export type profilePropsType = ReturnType<typeof mapStateToProps> & typeof mapDispatchToPropsLite

const SettingsContainer = (props: profilePropsType) => {

    const params:any = useParams()

    useEffect(() => {
        props.getProfileInformation(params.userId)

    }, [params.userId])
    return <Settings {...props}/>
}

const mapStateToProps = (state: RootState) => {
    return {
        ...state.profileSlice,
        authId: state.authSlice.id
    }
}

let mapDispatchToPropsLite = {
    getProfileInformation,
    putProfileStatus,
    putProfilePhoto,
    putProfileInformation
}

export default compose(
    connect<any,any,any,any>(mapStateToProps, mapDispatchToPropsLite),
)(SettingsContainer)