import {profileAPI} from "../../api/axiosAPI";
import {AppDispatch} from "../store";
import {profileSlice, profileState} from "../reducers/profileReducer";

export const getProfileInformation = (userId: number) => async (dispatch: AppDispatch) => {
    try {
        const profileResponse = await profileAPI.getProfileInformation(userId)
        const statusResponse = await profileAPI.getProfileStatus(userId)

        const concatProfileInformation = {...profileResponse, status: statusResponse}

        if (profileResponse !==undefined && statusResponse !==undefined) {
            dispatch(profileSlice.actions.setProfileInformation(concatProfileInformation))
        }
    } catch (e: any) {
        console.log('getProfileInformation error => ' + e.message)
    }
}


/** for Settings page **/
export const putProfileInformation = (setProfileInformation: profileState) => async (dispatch: AppDispatch) => {
    try {
        await profileAPI.putProfileInformation(setProfileInformation)
    } catch (e: any) {
        console.log('putProfileInformation error => ' + e.message)
    }
}

export const putProfileStatus = (status: string) => async (dispatch: AppDispatch) => {
    try {
        const response = await profileAPI.putProfileStatus(status)

        if (response) dispatch(profileSlice.actions.setProfileStatus(response))
    } catch (e: any) {
        console.log('putProfileStatus error => ' + e.message)
    }
}
export const putProfilePhoto = (userId: number, photoFile: File) => async (dispatch: AppDispatch) => {
    try {
        const response = await profileAPI.putProfilePhoto(photoFile)

        if (response.data.resultCode === 0) dispatch(getProfileInformation(userId))
    } catch (e: any) {
        console.log('putProfilePhoto error => ' + e.message)
    }
}
/** end for Settings page **/