import {AppDispatch} from "../store";
import {chatSlice} from "../reducers/chatReducer";


export const setShowSocketCat = (messageStatus: any) => async (dispatch: AppDispatch) => {
    try {
        dispatch(chatSlice.actions.setIsShowCat(messageStatus))
    } catch (e: any) {
        console.log('getNewMessageCount error => ' + e.message)
    }
}

export const setMessages = (message: any) => async (dispatch: AppDispatch) => {
    try {
        dispatch(chatSlice.actions.setMessages(message))
    } catch (e: any) {
        console.log('setMessages error => ' + e.message)
    }
}
