import {createSlice, PayloadAction} from "@reduxjs/toolkit";

interface appState {
    newMessageStatus: number, //to messages privReducer
    // messages: {message: string, photo: string, userId: number, userName: string}[] | null
    // messages: any, //to chat reducer
    appIsOnline: boolean,
    // isShowCat: boolean;
}

const initialState: appState = {
    newMessageStatus: 0,
    // messages: [],
    appIsOnline: false,
    // isShowCat: false,
}

export const appSlice = createSlice({
    name: 'app', initialState, reducers: {
        setAppIsOnline(state, action: PayloadAction<any>) {
            state.appIsOnline = action.payload
        },
        setNewMessageCount(state, action: PayloadAction<appState>) {
            state.newMessageStatus = action.payload.newMessageStatus
        },



    }
})

export default appSlice.reducer