import {createSlice, PayloadAction} from "@reduxjs/toolkit";

export interface profileState {
    aboutMe: string,
    contacts: {
        facebook: string,
        website: string,
        vk: string,
        twitter: string,
        instagram: string,
        youtube: string,
        github: string,
        mainLink: string
    },
    lookingForAJob: boolean,
    lookingForAJobDescription: string,
    fullName: string,
    userId: number,

    photos: {
        small: string,
        large: string
    },
    status: string
}

const initialState: profileState = {
    aboutMe: '',
    contacts: {
        facebook: '',
        website: '',
        vk: '',
        twitter: '',
        instagram: '',
        youtube: '',
        github: '',
        mainLink: ''
    }, lookingForAJob: false,
    lookingForAJobDescription: '',
    fullName: '',
    userId: 0,

    photos: {
        small: '',
        large: ''
    }, status: ''
}

export const profileSlice = createSlice({
    name: 'profile', initialState, reducers: {
        setProfileInformation(state, action: PayloadAction<profileState>) {
            state.aboutMe = action.payload.aboutMe

            state.contacts.facebook = action.payload.contacts.facebook
            state.contacts.website = action.payload.contacts.website
            state.contacts.vk = action.payload.contacts.vk
            state.contacts.twitter = action.payload.contacts.twitter
            state.contacts.instagram = action.payload.contacts.instagram
            state.contacts.youtube = action.payload.contacts.youtube
            state.contacts.github = action.payload.contacts.github
            state.contacts.mainLink = action.payload.contacts.mainLink

            state.lookingForAJob = action.payload.lookingForAJob
            state.lookingForAJobDescription = action.payload.lookingForAJobDescription
            state.fullName = action.payload.fullName
            state.userId = action.payload.userId

            state.photos.small = action.payload.photos.small
            state.photos.large = action.payload.photos.large

            state.status = action.payload.status

        }, setProfileStatus(state, action: PayloadAction<string>) {
            state.status = action.payload
        },


    }
})

export default profileSlice.reducer