import {Box, Button, Paper, Stack, Typography} from "@mui/material";
import {Field} from "redux-form";
import {CustomTextField, FileTextField} from "../Settings";
import style from './SettingsMobile.module.scss'
import {langTranslator} from "../../../utils/langTranslator";

export const SettingsMobile = (props: any) => {
    return <Box className={style.genBox} flex={5} p={2}>
        <form onSubmit={props.handleSubmit}>
            <Stack direction={'row'} justifyContent={'space-between'}>
                <Box flex={10}>
                    <Stack direction={'column'} justifyContent={'space-between'} spacing={8}>

                        <Box className={style.leftPanel} flex={1}>
                            {/*Good size 300x450*/}
                            <img className={style.doCenter} height={'auto'} src={{...props.photos}.large}/>
                            <Field className={style.doCenter} name="image" component={FileTextField} fullWidth={true}/>
                            <Box className={style.doCenter}>
                                <Field name={'fullName'} component={CustomTextField} placeholder={props.fullName} variant="standard"/>
                                <Field name={'aboutMe'} component={CustomTextField} placeholder={props.aboutMe} variant="standard"/>
                            </Box>
                        </Box>


                        <Box className={style.rightPanel} flex={3}>
                            <Paper className={style.paper}>
                                <Typography textAlign={'center'} variant={'h5'}>Редактирование профиля</Typography>

                                <Stack direction={'column'} justifyContent={'space-between'}>
                                    <Typography textAlign={'center'} variant={'body1'}>Сказать: </Typography>
                                    <Stack direction={'row'} justifyContent={'center'}>
                                        <Field name={'status'} component={CustomTextField} placeholder={props.status} variant="standard"/>
                                    </Stack>

                                </Stack>

                                <Stack direction={'column'} justifyContent={'space-between'}>
                                    <Typography textAlign={'center'} variant={'body1'}>Ищу работу: </Typography>

                                    <Stack direction={'row'} justifyContent={'center'}>
                                        <Field name={'lookingForAJob'} component={CustomTextField} placeholder={props.lookingForAJob ? 'true' : 'false'} variant="standard"/>
                                    </Stack>

                                </Stack>
                                <Stack direction={'column'} justifyContent={'space-between'}>
                                    <Typography textAlign={'center'} variant={'body1'}>Навыки: </Typography>
                                    <Stack direction={'row'} justifyContent={'center'}>
                                        <Field name={'lookingForAJobDescription'} component={CustomTextField} placeholder={props.lookingForAJobDescription} variant="standard"/>
                                    </Stack>

                                </Stack>
                                <br/>
                                <Typography textAlign={'center'}> Социальные сети:</Typography>
                                {Object.keys({...props.contacts}).map((key: any, index) => {
                                    const rusWord = langTranslator(key)

                                    return <Stack key={index} direction={'column'} justifyContent={'space-between'}>
                                        <Typography textAlign={'center'} variant={'body1'} key={key}>~{rusWord}: </Typography>
                                        <Stack direction={'row'} justifyContent={'center'}>
                                            <Field name={key} component={CustomTextField} placeholder={{...props.contacts}[key] === '' ? 'blank' : {...props.contacts}[key]} variant="standard"/>
                                        </Stack>
                                    </Stack>
                                })}


                                <br/>
                                <Stack direction={'row'} justifyContent={'center'} spacing={8}>
                                    <Button className={style.buttonSave} variant="contained" type={'submit'}>save</Button>
                                </Stack>

                            </Paper>
                        </Box>

                    </Stack>
                </Box>

            </Stack>
        </form>
    </Box>
}