export const langTranslator = (engWord: string) => {
    let rusWord = engWord
    if (engWord === 'facebook') rusWord = 'фэйсбук'
    if (engWord === 'website') rusWord = 'сайт'
    if (engWord === 'vk') rusWord = 'в контакте'
    if (engWord === 'twitter') rusWord = 'твиттер'
    if (engWord === 'instagram') rusWord = 'инстаграм'
    if (engWord === 'youtube') rusWord = 'ютуб'
    if (engWord === 'github') rusWord = 'гитхаб'
    if (engWord === 'mainLink') rusWord = 'другое'
    return rusWord
}