import {Field, reduxForm} from "redux-form";
import {Navigate} from "react-router-dom";
import {Box, Button, Stack, TextField, Typography} from "@mui/material";
import {authAPI} from "../../api/axiosAPI";
import {useState} from "react";
import {useIsDesktop} from "../../hooks/responsive";
import style from './Login.module.scss'

const CustomTextField = (props: any) => {
    const {input, meta, child, ...restProps} = props
    const validatorError = meta.touched && meta.error

    return (
        <Box>
            <TextField label="" variant="outlined" {...input} {...restProps}/>
            {validatorError && <span>{meta.error}</span>}
        </Box>
    )
}

const LoginForm = (props: any) => {

    return (
        <form onSubmit={props.handleSubmit}>
            <Stack className={style.formPlace} direction={'column'} justifyContent={'space-between'}>
                <Field style={{marginTop: "10px"}} label="Email" name={'email'} component={CustomTextField}/>
                <Field style={{marginTop: "10px"}} label="Пароль" name={'Password'} component={CustomTextField} type={'password'}/>
                <Stack className={style.remember} direction={'row'}>
                    <Field type={'checkbox'} name={'rememberMe'} component={'input'}/>
                    <Typography component={'span'}>Запомнить меня</Typography>
                </Stack>

                {props.error && <Box>{props.error}</Box>}
                <Button style={{marginTop: "10px"}} variant={'contained'} color={'success'} type={'submit'}>Войти</Button>
            </Stack>
        </form>
    );
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)


export const Login = (props: any) => {
    const isDesktop = useIsDesktop()

    const onSubmit = (formData: any) => {
        // if (!formData.rememberMe) formData.rememberMe = false
        props.doLogin(formData.email, formData.Password, false)
    }

    const [devMode, setDevMode] = useState(0)
    //Костыль с навигацией
    if (props.isAuth) return <Navigate to={'/Profile/' + props.id}/>
    return <Box flex={5} p={2}  width={'100%'} marginTop={'20vh'}>
        <Stack direction={'row'} justifyContent={'space-between'}>
            <Box flex={10} >
                <Stack direction={'row'} justifyContent={'center'}>
                    <Box>
                        <Typography onClick={e=>setDevMode(devMode + 1)} textAlign={'center'} variant={'h5'}>Авторизация:</Typography><br/>
                        {/*кастыль*/}
                        <p style={{position: "absolute", marginLeft:'-90px'}}>тестовые логин и пароль: free@samuraijs.com, free</p><br/><br/>
                        {/*кастыль end*/}
                        {/*<input className={style.apiKey} onFocus={e=>e.target.placeholder = ''} placeholder={'   Ваш API-key'} type={'password'} onBlur={e => {authAPI.setAPIKey(e.target.value);e.target.placeholder = '   Введите ваш API-key'}}/>*/}
                        <LoginReduxForm onSubmit={onSubmit}/>
                    </Box>
                </Stack>
            </Box>

            {isDesktop && <Box flex={3}/>}
        </Stack>
    </Box>


}