import {
    Box,
    FormControlLabel,
    List,
    ListItem,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    Switch
} from "@mui/material";
import {NavLink} from "react-router-dom";
import PermIdentityOutlinedIcon from '@mui/icons-material/PermIdentityOutlined';
import SettingsApplicationsOutlinedIcon from '@mui/icons-material/SettingsApplicationsOutlined';
import ChatBubbleOutlineIcon from '@mui/icons-material/ChatBubbleOutline';
import GroupOutlinedIcon from '@mui/icons-material/GroupOutlined';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import sleepCat from '../../assets/img/sleepyCat.gif'
import {
    useIsDesktop,
    useIsMobile,
    useIsPortrait,
    useIsRetina,
    useIsTabletMax,
    useIsTabletMin
} from "../../hooks/responsive";
import {FailLoading} from "../common/FailLoading/FailLoading";
import style from'./Sidebar.module.scss'
import catIcon from '../../assets/img/catIcon.png'

const MenuNavTo = (props: any) => {
    return <NavLink className={style.linkDecoration} to={props.linkTo}>
        <ListItem disablePadding>
            <ListItemButton>
                <ListItemIcon>
                    {props.icon}
                </ListItemIcon>
                <ListItemText primary={props.menuName}/>
                {props.isShowCat ? <img src={catIcon} width={'30px'}/> : <div/>}
            </ListItemButton>
        </ListItem>
    </NavLink>
}


export const Sidebar = (props: any) => {
    const isDesktop = useIsDesktop()

    const isTabletMin = useIsTabletMin()
    const isTabletMax = useIsTabletMax()
    const isTablet = isTabletMin && isTabletMax

    const isMobile = useIsMobile()

    const isPortrait = useIsPortrait()
    const isRetina = useIsRetina()

    if (isDesktop) console.log('isDesktop')
    if (isTablet) console.log('isTablet')
    if (isMobile) console.log('isMobile')


    if (!isMobile) return <Box flex={1} p={2} sx={{display: {xs: 'block', sm: 'block'}}}>

        <List>
            {
                props.isAuth
                    ? <MenuNavTo linkTo={'/Profile/' + props.authId} menuName={'Профиль'} icon={<PermIdentityOutlinedIcon/>}/>
                    : <MenuNavTo linkTo={'/Login'} menuName={'Профиль'} icon={<PermIdentityOutlinedIcon/>}/>
            }

            {
                props.isAuth
                    ? <MenuNavTo linkTo={'/Devs'} menuName={'Пользователи'} icon={<GroupOutlinedIcon/>}/>
                    : <MenuNavTo linkTo={'/Login'} menuName={'Пользователи'} icon={<GroupOutlinedIcon/>}/>
            }

            {
                props.isAuth
                    ? <MenuNavTo isShowCat={props.isShowCat} linkTo={'/Chat'} menuName={'Общий чат'} icon={<ChatBubbleOutlineIcon/>}/>
                    : <MenuNavTo isShowCat={props.isShowCat} linkTo={'/Login'} menuName={'Общий чат'} icon={<ChatBubbleOutlineIcon/>}/>
            }

            {
                props.isAuth
                    ? <MenuNavTo linkTo={'/PrivateMessage'} menuName={'Личные сообщения'} icon={<MailOutlineIcon/>}/>
                    : <MenuNavTo linkTo={'/Login'} menuName={'Личные сообщения'} icon={<MailOutlineIcon/>}/>
            }
            {/*{*/}
            {/*    props.isAuth*/}
            {/*        ? <MenuNavTo linkTo={'/PrivateMessage'} menuName={'Личные сообщения +' + props.newMessageStatus} icon={<MailOutlineIcon/>}/>*/}
            {/*        : <MenuNavTo linkTo={'/Login'} menuName={'Личные сообщения +' + props.newMessageStatus} icon={<MailOutlineIcon/>}/>*/}
            {/*}*/}

            {
                props.isAuth
                    ? <MenuNavTo linkTo={'/Settings/' + props.authId} menuName={'Настройки'} icon={<SettingsApplicationsOutlinedIcon/>}/>
                    : <MenuNavTo linkTo={'/Login'} menuName={'Настройки'} icon={<SettingsApplicationsOutlinedIcon/>}/>
            }
        </List>
        <Box>
            <img className={style.catImage} src={sleepCat}></img>
            <Box className={style.codeBin}>01010101010101010101101010</Box>
        </Box>


        <FormControlLabel disabled control={<Switch defaultChecked onChange={e => props.setMode(props.mode === 'light' ? 'dark' : 'light')}/>} label="Light mode"/>
    </Box>

    return <FailLoading/>
}