import NavbarContainer from "./components/Navbar/NavbarContainer";
import {BrowserRouter, Route, Routes} from 'react-router-dom';
import React, {useEffect, useState} from "react";
import {connect} from "react-redux";
import {Box, Stack} from "@mui/material";
import SidebarContainer from "./components/Sidebar/SidebarContainer";
import LoginContainer from "./components/Login/LoginContainer";
import ProfileContainer from "./components/Profile/ProfileContainer";
import SettingsContainer from "./components/Settings/SettingsContainer";
import MessagesContainer from "./components/Chat/ChatContainer";
import PrivateMessageContainer from "./components/PrivateMessageBeta/PrivateMessagesContainer";
import catSound from "./assets/sound/newMessage.wav";
import {withSuspense} from "./hocs/withSuspense";
import {
    getNewMessageCount,
    setAppIsOnline,
} from "./redux/action-creators/appAction";
import {getAuthInformation} from "./redux/action-creators/authAction";
import './App.scss'
import {sideEffect_setScrollDown} from "./sideEffects/sideEffect_setScrollDown";
import {checkPathname} from "./utils/checkPathname";
import {setMessages, setShowSocketCat} from "./redux/action-creators/chatAction";

//lazy loading
const UsersContainer = React.lazy(() => import('./components/Users/UsersContainer'));

export let MessageWebSocket: WebSocket | null = null //need fix global
const Url = 'wss://social-network.samuraijs.com/handlers/ChatHandler.ashx'

const App = (props: any) => {

    //first settings
    useEffect(()=>{
        props.getAuthInformation()

        window.addEventListener('offline', e => props.setAppIsOnline('offline'))
        window.addEventListener('online', e => props.setAppIsOnline('online'))
    },[])

    //global listener chat for sound and get new message
    useEffect(() => {
        if (props.authId !== 0){

            props.setMessages([])
            MessageWebSocket = new WebSocket(Url)

            MessageWebSocket && MessageWebSocket.addEventListener('message', (e: MessageEvent) => {
                props.setMessages([...JSON.parse(e.data)])

                if(checkPathname() === '/Chat') {
                    sideEffect_setScrollDown('messageArea')
                    props.setShowSocketCat(false)
                }

                if((props.authId !== 0) && (props.authId !== [...JSON.parse(e.data)][0].userId) ){

                    if([...JSON.parse(e.data)].length === 1){
                        const AudioPlay = new Audio(catSound);
                        AudioPlay.play()

                        props.setShowSocketCat(true)
                    }

                }

            })
        }
    }, [props.authId])

    return (
        <BrowserRouter>
            <Box sx={{height: '100%'}}>

                <NavbarContainer/>
                <Stack direction={'row'} justifyContent={'space-between'}>
                    <SidebarContainer/>

                    <Routes>
                        <Route path={'/'} element={withSuspense(LoginContainer)}/>
                        <Route path={'/Profile'} element={withSuspense(ProfileContainer)}/>
                        <Route path={'/Profile/:userId'} element={withSuspense(ProfileContainer)}/>
                        <Route path={'/Devs'} element={withSuspense(UsersContainer)}/>
                        <Route path={'/login'} element={withSuspense(LoginContainer)}/>
                        <Route path={'/Settings/:userId'} element={withSuspense(SettingsContainer)}/>
                        <Route path={'/Chat'} element={withSuspense(MessagesContainer)}/>
                        <Route path={'/PrivateMessage'} element={withSuspense(PrivateMessageContainer)}/>
                        <Route path={'/PrivateMessage/PrivText/:recipientId'}
                               element={withSuspense(PrivateMessageContainer)}/>
                    </Routes>

                </Stack>

            </Box>
        </BrowserRouter>

    )
}


const mapStateToProps = (state: any) => ({
    newMessageStatus: state.appSlice.newMessageStatus,
    isAuth: state.authSlice.isAuth,
    authId: state.authSlice.id,
})

const mapDispatchToPropsLite = {
    getNewMessageCount,
    getAuthInformation,
    setAppIsOnline,
    setMessages,
    setShowSocketCat
}
//если роутинг барахлит обернуть еще и в withRouter
export default connect(mapStateToProps, mapDispatchToPropsLite)(App);
