import {
    AppBar,
    Box,
    IconButton,
    Menu,
    MenuItem, Stack,
    Toolbar,
    Tooltip, Typography
} from "@mui/material";
import React from "react";
import {NavLink} from "react-router-dom";
import devIcon from "../../../assets/img/devIcon.png";
import MenuIcon from '@mui/icons-material/Menu';
import style from './NavbarMobile.module.scss'


const MenuProfile = (props: any) => {
    return <Box sx={{flexGrow: 0}}>
        <Tooltip title="Open settings">
            <IconButton onClick={e => props.setIsMenuOpen(true)} sx={{p: 0}}>
                {/*<Avatar alt="Remy Sharp" src={props.avatarPhoto}/>*/}
                <MenuIcon />
            </IconButton>
        </Tooltip>
        <Menu
            className={style.menu}
            anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
            }}
            keepMounted
            transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
            }}
            open={props.isMenuOpen}
            onClose={e => props.setIsMenuOpen(false)}
        >
            <MenuItem>
                <NavLink to={'/Profile/' + props.authId} style={{textDecoration: 'none', color: 'inherit'}}>
                    <Typography onClick={e=>props.setIsMenuOpen(false)} >Профиль</Typography>
                </NavLink>

            </MenuItem>
            <MenuItem>
                <NavLink to={'/Devs'} style={{textDecoration: 'none', color: 'inherit'}}>
                <Typography onClick={e=>props.setIsMenuOpen(false)} >Пользователи</Typography>
                </NavLink>
            </MenuItem>
            <MenuItem>
                <NavLink to={'/Chat'} style={{textDecoration: 'none', color: 'inherit'}}>
                <Typography onClick={e=>props.setIsMenuOpen(false)} >Общий чат</Typography>
                </NavLink>
            </MenuItem>
            <MenuItem>
                <NavLink to={'/PrivateMessage'} style={{textDecoration: 'none', color: 'inherit'}}>
                <Typography onClick={e=>props.setIsMenuOpen(false)} >Личные сообщения</Typography>
                </NavLink>
            </MenuItem>
            <MenuItem>
                <NavLink to={'/Settings/' + props.authId} style={{textDecoration: 'none', color: 'inherit'}}>
                <Typography onClick={e=>props.setIsMenuOpen(false)} >Настройки</Typography>
                </NavLink>
            </MenuItem>
            <MenuItem>
                {/*<Typography onClick={e=>props.setIsMenuOpen(false)} textAlign="Log out" onClick={props.doLogout}>Выйти</Typography>*/}
                <Typography onClick={props.doLogout()}>Выйти</Typography>
            </MenuItem>

        </Menu>
    </Box>
}

export const NavbarMobile = (props: any) => {
    console.log(props.avatarPhoto)
    const [isMenuOpen, setIsMenuOpen] = React.useState(false)

    return <AppBar position={"sticky"}>
        <Toolbar className={style.toolbar}>
            {/*<Box component={'p'}>Samurai-dev-network</Box>*/}
            <Stack direction={'row'} spacing={1}>
                <img className={style.image} src={devIcon}/>
                <Typography component={'p'}>SamuraiJS</Typography>
            </Stack>
            {
                props.isAuth
                    ? <Box>
                        <MenuProfile doLogout={props.doLogout} isMenuOpen={isMenuOpen} setIsMenuOpen={setIsMenuOpen} avatarPhoto={props.avatarPhoto} authId={props.authId}/>
                        {/*<Typography>{props.login}</Typography>*/}
                    </Box>
                    :
                    <NavLink to={'/login'}>login</NavLink>
            }

        </Toolbar>
    </AppBar>
}