import {NavLink, useLocation, useParams} from "react-router-dom";
import {
    Box,
    Stack,
    Table,
    TableContainer,
    TextField,
    Typography,
    Avatar,
    Paper
} from "@mui/material";
import SendIcon from '@mui/icons-material/Send';
import React, {useEffect, useRef} from "react";
import {checkPathname} from "../../../utils/checkPathname";
import userLogo from '../../../assets/img/user.png'


const MessageOther = (props: any) => {
    //console.log('>>>>>>>>Message Other<<<<<<<<<<<<<')
    return (
        <Stack direction={'row'} justifyContent={'left'} spacing={2} sx={{width: '100%'}} marginBottom={3}>
            <Stack direction={'column'} justifyContent={'center'}>
                <Avatar alt="Remy Sharp" src={props.photo ? props.photo : userLogo}/>
                <Typography>{props.name}</Typography>
            </Stack>

            <Stack direction={'column'} justifyContent={'center'} sx={{width: '100%'}}>
                <Paper elevation={3}>
                    <Typography sx={{wordBreak: 'break-all'}} padding={2}>{props.message}</Typography>
                </Paper>
            </Stack>
        </Stack>
    )
}

const MessageOwner = (props: any) => {
    //console.log('>>>>>>>>Message Owner<<<<<<<<<<<<<')
    return <Stack direction={'row'} justifyContent={'left'} spacing={2} sx={{width: '100%'}}
                  marginBottom={3}>
        <Stack direction={'column'} justifyContent={'center'} sx={{width: '100%'}}>
            <Paper elevation={3}>
                <Typography sx={{wordBreak: 'break-all'}} padding={2}>{props.message}</Typography>
            </Paper>
        </Stack>

        <Stack direction={'column'} justifyContent={'center'}>
            <Avatar alt="Remy Sharp" src={props.photo ? props.photo : userLogo}/>
            <Typography>{props.name}</Typography>
        </Stack>
    </Stack>
}

export const PrivText = (props: any) => {
    const params = useParams()
    const newMessage = useRef('')

    const location: any = useLocation();
    const {state} = location;

    //костыль получение сообщений по таймеру
    useEffect(() => {
        props.setFullDialogsWithIdThunk(params.recipientId)
        // sideEffect_setScrollDown('messageArea')

        if (checkPathname() === '/PrivateMessage/PrivText/' + params.recipientId){
            const timer = setInterval(() => {
                props.setFullDialogsWithIdThunk(params.recipientId)//если не афк то, получить список всех сообщений
            }, 5000);
            return () => clearInterval(timer);
        }

    }, []);


// return <div>new page</div>
    return <Box flex={5} p={2}>

        <Stack direction={'row'} justifyContent={'space-between'}>

            {/*LEFT PANEL*/}
            <Box flex={10}>

                {/*general stack items*/}
                <Stack height={'100%'} direction={'column'} justifyContent={'space-between'}>

                    {/*message area*/}
                    <Box flex={10} marginRight={4}>

                        <Paper sx={{height: '100%', width: '100%'}}>
                            <Stack direction={'row'} justifyContent={'space-between'} padding={3}>
                                {/*view messasges conponent*/}


                                <Stack direction={'column'} justifyContent={'space-between'} sx={{width: '100%'}}>
                                    {/*view all messages*/}
                                    <TableContainer
                                        id={'messageArea'}
                                        sx={{
                                            height: 'calc(75vh)',
                                            'overflow-y': 'scroll',
                                            'scrollbar-color': '#1976d2 white',
                                            'scrollbar-width': 'thin'
                                        }}
                                    >
                                        <Box sx={{height: "max-content"}}>
                                            {
                                                props.fullDialogWithId.map((u: any, index: any) => {

                                                    // if (u.userId === null) return <Typography>Message is clear</Typography>
                                                    console.log(u)

                                                    if (u.senderId === props.authId) {
                                                        return <NavLink key={index} to={'/Profile/' + u.senderId}><MessageOwner
                                                             id={u.senderId}
                                                            name={u.senderName}
                                                            photo={props.smallPhotoOwner}
                                                            message={u.body}/></NavLink>
                                                    } else {
                                                        return <NavLink key={index} to={'/Profile/' + u.senderId}><MessageOther
                                                             id={u.senderId}
                                                            name={u.senderName}
                                                            photo={state && state.senderPhotoById ? state.senderPhotoById : ''}
                                                            message={u.body}/></NavLink>
                                                    }

                                                })
                                            }


                                        </Box>
                                    </TableContainer>


                                </Stack>
                            </Stack>
                        </Paper>

                    </Box>
                    <Box color={'white'}>fix here bug</Box>

                    {/*Button send + TextField send messages*/}
                    <Box>
                        <Stack direction={'row'} justifyContent={'space-between'}>

                            <Box flex={32}><TextField onBlur={e => newMessage.current = e.target.value}
                                                      label="You message" color="secondary" focused fullWidth/></Box>


                            <Box flex={1} marginLeft={1}>
                                <Stack direction={'column'} justifyContent={'center'} sx={{height: '100%'}}>
                                    <SendIcon onClick={e => {
                                        props.sendMessageById(params.recipientId, newMessage.current)
                                        newMessage.current = ''
                                        props.setFullDialogsWithIdThunk(params.recipientId)
                                    }}/>
                                </Stack>
                            </Box>
                        </Stack>
                    </Box>

                </Stack>

            </Box>

        </Stack>


    </Box>
}