import {authAPI} from "../../api/axiosAPI";
import {authSlice} from "../reducers/authReducer";
import {AppDispatch} from "../store";

export const getAuthInformation = () => async (dispatch: AppDispatch) => {
    try {
        const response = await authAPI.getAuthInformation()
        if (response.data.resultCode === 0) {
            const {id, login, email} = response.data.data

            dispatch(authSlice.actions.setAuthInformation({id, login, email, isAuth: true}))
        }
    } catch (e: any) {
        console.log('getAuthInformation error => ' + e.message)
    }
}

export const doLogin = (email: string, password: string, rememberMe: boolean) => async (dispatch: AppDispatch) => {
    try {
        const response = await authAPI.doLogin(email, password, rememberMe)
        if (response.data.resultCode === 0) {
            dispatch(getAuthInformation())
        }
    } catch (e: any) {
        console.log('doLogin error => ' + e.message)
    }
}

export const doLogout = () => async (dispatch: AppDispatch) => {
    try {
        const response = await authAPI.doLogout()
        if (response.data.resultCode === 0) {
            window.location.href = '/'
            dispatch(authSlice.actions.setAuthInformation({id: 0, email: '', login: '', isAuth: false}))
        }
    } catch (e: any) {
        console.log('doLogout error => ' + e.message)
    }
}