import {useMediaQuery} from "react-responsive";

export const useIsDesktop = () => useMediaQuery({query: "(min-width: 1224px)"})

export const useIsTabletMax = () => useMediaQuery({query: "(max-width: 1224px)"})
export const useIsTabletMin = () => useMediaQuery({query: "(min-width: 786px)"})
// const isTablet = (useIsTabletMin && useIsTabletMax)

export const useIsMobile = () => useMediaQuery({query: "(max-width: 786px)"})

export const useIsPortrait = () => useMediaQuery({query: "(orientation: portrait)"})
export const useIsRetina = () => useMediaQuery({query: "(max-resolution: 300dpi)"})