import {Box, TextField} from "@mui/material";
import {reduxForm} from "redux-form";

import {
    useIsDesktop,
    useIsMobile,
    useIsPortrait,
    useIsRetina,
    useIsTabletMax,
    useIsTabletMin
} from "../../hooks/responsive";
import {SettingsDesktop} from "./desktop/SettingsDesktop";
import {SettingsTablet} from "./tablet/SettingsTablet";
import {SettingsMobile} from "./mobile/SettingsMobile";
import {FailLoading} from "../common/FailLoading/FailLoading";

export const FileTextField = ({floatingLabelText, fullWidth, input, label, meta: {touched, error}, ...custom}: any) => {
    // if (input.value && input.value[0] && input.value[0].name) {
    //     floatingLabelText = input.value[0].name;
    // }
    delete input.value;
    return (
        <TextField
            // hintText={label}
            // floatingLabelText={floatingLabelText}
            type="file"
            // errorText={error}
            {...input}
            {...custom}
        />
    )
}

export const CustomTextField = (props: any) => {
    const {input, meta, child, ...restProps} = props
    const validatorError = meta.touched && meta.error

    return (
        <Box>
            <TextField label="" variant="outlined" {...input} {...restProps}/>
            {validatorError && <span>{meta.error}</span>}
        </Box>
    )
}

const SettingsDesktopReduxForm = reduxForm({form: 'SettingsDesktop'})(SettingsDesktop)
const SettingsTabletReduxForm = reduxForm({form: 'SettingsTablet'})(SettingsTablet)
const SettingsMobileReduxForm = reduxForm({form: 'SettingsMobile'})(SettingsMobile)


const SettingsUI = (props: any) => {
    const onSubmit = (formData: any) => {

        const profieInfo = {
            userId: formData.userId ? formData.userId : props.userId,
            lookingForAJob: formData.lookingForAJob ? formData.lookingForAJob : props.lookingForAJob,
            lookingForAJobDescription: formData.lookingForAJobDescription ? formData.lookingForAJobDescription : props.lookingForAJobDescription,
            fullName: formData.fullName ? formData.fullName : props.fullName,
            contacts: {
                github: formData.github ? formData.github : props.contacts.github,
                vk: formData.vk ? formData.vk : props.contacts.vk,
                facebook: formData.facebook ? formData.facebook : props.contacts.facebook,
                twitter: formData.twitter ? formData.twitter : props.contacts.twitter,
                instagram: formData.instagram ? formData.instagram : props.contacts.instagram,
                website: formData.website ? formData.website : props.contacts.website,
                youtube: formData.youtube ? formData.youtube : props.contacts.youtube,
                mainLink: formData.mainLink ? formData.mainLink : props.contacts.mainLink,
            },
            AboutMe: formData.aboutMe ? formData.aboutMe : props.aboutMe
        }

        if (formData.status)
            props.putProfileStatus(formData.status)
        //update profile information
        if (profieInfo)
            props.putProfileInformation(profieInfo)
        //change photo
        if (formData.image[0])
            props.putProfilePhoto(props.authId, formData.image[0])
    }

    return <props.renderComponent {...props} onSubmit={onSubmit}/>

}

const Settings = (props: any) => {
    const isDesktop = useIsDesktop()

    const isTabletMin = useIsTabletMin()
    const isTabletMax = useIsTabletMax()
    const isTablet = isTabletMin && isTabletMax

    const isMobile = useIsMobile()

    const isPortrait = useIsPortrait()
    const isRetina = useIsRetina()

    if (isDesktop) console.log('isDesktop')
    if (isTablet) console.log('isTablet')
    if (isMobile) console.log('isMobile')

    let renderComponent = undefined
    if (isDesktop) renderComponent = SettingsDesktopReduxForm
    if (isTablet) renderComponent = SettingsTabletReduxForm
    if (isMobile) renderComponent = SettingsMobileReduxForm

    if (isDesktop) return <SettingsUI renderComponent={renderComponent} {...props}/>
    if (isTablet) return <SettingsUI renderComponent={renderComponent} {...props}/>
    if (isMobile) return <SettingsUI renderComponent={renderComponent} {...props}/>


    return <FailLoading/>


}
export default Settings

